import Vue from 'vue'
const ImessageConstructor = Vue.extend(require('./imessage.vue').default) // VueVue.extend

const Imessage = (message) => {

	const ImessageInstance = new ImessageConstructor({
		data: Object.assign({},message)
	})
	ImessageInstance.vm = ImessageInstance.$mount() // domVue
	ImessageInstance.vm.visible = true
	ImessageInstance.dom = ImessageInstance.vm.$el
	document.body.appendChild(ImessageInstance.dom) // dombody

	return ImessageInstance.vm
}

export default {
	install: Vue => {
		Vue.prototype.$imessage = Imessage // ImessageVueprototype
	}
}


